.card {
    width: 250px;
    height: 110px;
    border: 1px solid #ccc !important;
    background-color: transparent !important; }
.card-body {
    padding: 1rem; }
.card-titulo {
    text-align: center;
    font-size: 1em; }
.card-numero {
    font-size: 1.7em;
    text-align: center; }
.suspect {
    .card-numero {
        color: #4C994C; } }
.confirm {
    .card-numero {
        color: #cc0000; } }
